lity to see humor in the\n          profession and laugh.\n        ")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("\n          A long-time teacher of prize-winning students in Irvine, California, Dr. Smith’s students were the\n          California State Champions for many years between 1988-2009. Former students have been First Prize winners\n          in the Naftsger Competition, Tchaikovsky Competition, the Bachauer Competition (Juilliard) and in MTNA\n          competitions. A chamber music coach as well as piano teacher, Scott McBride Smith coached the Wakarusa Trio\n          to the 2013 First Place in the MTNA Chamber Music (Strings) national competition. The group went on the win\n          the Coleman-Barstow Prize at the 2014 Coleman Chamber Music Competition. Another Smith-coached group, Trio\n          Soave, won Third Place in the 2016 MTNA Chamber Music (Strings) national competition. Private piano students\n          were MTNA National Finalists in 2017 and 2018.\n        ")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("\n          Dr. Smith is co-author of the college text The Well-Tempered Keyboard Teacher and associate editor of the\n          magazine Cla\n        ")]),_vm._v(" "),_c('p',{staticClass:"text-gray padding-top15"},[_vm._v("\n          Dr. Smith has served as Visiting Professor at the Shanghai Conservatory and the Sichuan Conservatory in\n          China; the Béla Bartók Conservatory in Lima, Peru; at the Hong Kong Academy of Performing Arts; and in\n          Taiwan, sponsored by the International Piano Teaching Foundation.\n          Scott McBride Smith received his doctorate from the University of Southern California, where he was\n          co-winner of the Outstanding Graduate in Piano award.\n        ")])])])}]
